import React from 'react'
import './Form.css';
const Form = props=>{
    return(
        <React.Fragment>
              <form onSubmit={props.loadweather}>
              <div className="step1 " >
              <h4 style={{
                    color:"tan",
                    textAlign:"center",
                    marginTop:"20px",
                    marginBottom:"30px"
                    }}>How is the weather today!!</h4>
                    <div className="inputstyles">
                        <label >City</label>
                        <input type="text" name="city"autocomplete="off"/>
                    </div>
                    <div >
                        <label>Country</label>
                        <input type="text" name="country" autocomplete="off"/>
                    </div>
                    <div className="btndiv">
                        <button className="btn-style" >Submit</button>
                    </div>
                </div>
              </form>  
          
        </React.Fragment>
    );
};
function error(){
  return(
    <div className="alert alert-danger " role="alert">
      Please enter City and Country
    </div>
  )
}
export default Form;