import React from 'react';
import './Weather.css';
const Weather =(props)=> { 
    
        return(
          <React.Fragment>
            <div className="result">
              <div>
              <h3 style={{
                    color:"rgb(213, 233, 187)",
                    textAlign:"center",
                    marginTop:"20px",
                    marginBottom:"30px"
                    }}>Your Result Will Appear Here :</h3>
                
                {	
	 	              props.city && props.country && 
                   <h5> Location: 
	 	            	<span> { props.city }, { props.country }</span>
	 	              </h5> 
	              }
            	 { 	
                  props.temp_celsius &&
                   <h5> Temperature: 
                    <span> { props.temp_celsius }&deg;	</span>
                  </h5> 
                  
                }
                
                { 	
                  props.temp_max && <h5> Max Temparature: 
                    <span> { props.temp_max } </span>
                  </h5> 
                }
                 { 	
                  props.temp_min && <h5> Min Temparature: 
                    <span> { props.temp_min } </span>
                  </h5> 
                }
                 { 	
                  props.description && <h5> Weather Type: 
                    <span> { props.description } </span>
                  </h5> 
                }
                
              </div> 
            </div>
           
  
 </React.Fragment>
       )
}
export default Weather;
